.app.dark{
    background-color: #222;
    color: rgb(156, 156, 156);

    .chartGrid{
        stroke: rgb(228, 228, 228,0.219);
    }
    .table{
        background-color: #111;

        .tableCell{
            color: gray;
        }
    }
    .navbar{
        color: #999;
        border-color: #333;

        .search{
            border-color: #333;
        }
    }
    .sidebar{
        background-color: #111;
        border-color:#333 ;

        .top{
            .logo{
                color: #999;
            }
        }
        hr{
            border-color: #222;
        }
        ul{
            li{
                &:hover{
             background-color: #333;
                }
                .icon{
                    color: #999;
                }
            }
        }
    }
    .datatable{
        .dataGrid{
          color: gray;
          border: none;

          .viewButton,.deleteButton,.cellWithStatus{
            color: gray;
            border: none;
          }
        }
    }
    input{
        background-color: transparent;
    }
}